* {
    margin: 0;
    padding: 0;
}
.App{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.calculator{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding: 5rem;
    box-shadow: -30px 20px 100px 10px #FB184E78;
    border-radius: 30px;
}
input {
    padding: 0.5rem;
}
.btn{
    display: flex;
    gap: 1rem;
}
.btn button{
    background-color: black;
    color: white;
    padding: 0.6rem 0.8rem;
    border: none;
}
.error {
    color: red;
}
.success {
    color: #206cff;
}
